<template>
  <div class="ball-container">
    <transition
      name="drop"
      @after-enter="beginDrop"
      @after-leave="afterDrop"
    >
      <div class="ball" v-show="show" ref='ball'></div>
    </transition>
  </div>
</template>

<script>
export default {
  name: 'ball',
  data() {
    return {
      show: false
    }
  },
  methods: {
    // ()
    drop(event) {
      const rect = event.target.getBoundingClientRect()
      this.$refs.ball.style.left = `${rect.left}px`
      this.$refs.ball.style.top = `${rect.top}px`
      this.show = true
    },
    // 
    beginDrop() {
      this.show = false
    },
    // 
    afterDrop() {
      this.remove()
    }
  }
}
</script>

<style lang="stylus" scoped>
@import '~common/stylus/mixin'
@import '~common/stylus/variable'
.ball-container
  .ball
    position: fixed
    z-index: 200
    width: 16px
    height: 16px
    margin: 10px
    border-radius: 50%
    background: $color-blue
    // 
    &.drop-leave-active
      // transition
      // topleft
      transition: top 0.4s cubic-bezier(0.49, -0.29, 0.75, 0.41), left 0.4s linear,opacity 0.2s ease-in 0.2s
    &.drop-leave-to
      // !importantjsstylestyly
      left: 22px !important
      top: 645px !important
      opacity:0
</style>
